<template>
    <div class='grid columns-1 gap-1'>
        <div class='d-flex gap-1'>
            <input v-model='filterText' class='form-control' placeholder='Enter text to filter results'/>
            <button 
                class='btn btn-danger text-white fa fa-times fw-8' 
                @click='filterText = ""'
                v-tooltip='"Clear Search"'
            />
        </div>
        <div class='grid columns-3 gap-1 col-12'>
            <button 
                v-for='node in nodeList' 
                @click='selectedModel = node'
                class='p-1 btn btn-accent-dk d-flex gap-1 align-items-center fw-8'
                :class='{active: selectedModel && node.title == selectedModel.title}'
            >
                <!-- <i class='fa fa-cogs'></i> -->
                {{node.title}}
            </button>
        </div>
        <span v-if='nodeList.length ==0' class='fw-8'>
            No elements found containing {{filterText}}.
        </span>
    </div>
</template>
<script>
// @ts-no-check
import { vModel } from "@/pockets/utils/v-model"
import { inject, ref, computed } from "vue"

let setup = (props, { emit } )  => { 
    
    let filterText = ref('')

    let editor = inject('pockets-node-tree-editor')
    
    let nodeList = computed(() => {
        return Object.values(editor.nodes.list).filter((e) => {
            if(filterText.value == '') return true
            return e.title.toLowerCase().includes(filterText.value.toLowerCase())
        })
    })

    return { 
        filterText,
        nodeList,
        selectedModel: vModel(props, emit, 'selected'), 
    }

}
export default {
    setup, 
    props: {
        selected: {
            type: [Boolean, Object],
            required: true
        }
    }
}
</script>
