
import { vModel } from "@/pockets/utils/v-model"

let setup = (props, { emit }) => {
    let locationModel = vModel(props, emit, 'location')
    return {
        setLocation(location: string){
            emit(`${location}`)
            locationModel.value = location
        },
    }
}
export default {
    inheritAttrs: false,
    setup,
    props: {
        location: {
            type: [Boolean, String],
            default: false
        },
        api: {
            required: true,
        }  
    }
}
